.rightBar {
  flex: 2.5;
}

.rightBarWrapper {
  padding: 20px 20px 0 0;
}

.birthdayContainer {
  display: flex;
  align-items: center;
}

.birthdayImg {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.birthdayText {
  font-weight: 300;
  font-size: 15px;
}

.rightBarAd {
  width: 100%;
  border-radius: 10px;
  margin: 30px 0;
}

.rightBarTitle {
  margin-bottom: 20px;
}

.rightBarFriendList {
  padding: 0;
  margin: 0;
  list-style: none;
}

.rightBarTitle {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 20px;
}

.rightBarInfo {
  text-align: center;
  margin-bottom: 30px;
}

.rightBarInfoItem {
  margin-bottom: 10px;
}

.rightBarInfoKey {
  font-weight: 500;
  margin-right: 15px;
  color: #555;
}

.rightBarInfoValue {
  font-weight: 300;
}

.rightBarFollowings {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.rightBarFollowing {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  cursor: pointer;
  width: 130px;
  justify-content: center;
  align-items: center;
}

.rightBarFollowingImg {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 5px;
  transform: scale(1);

  transition: all 250ms ease;
}

.rightBarFollowingImg:hover {
  transform: scale(1.05);
}

.rightBarFriend {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.rightBarProfileImgContainer {
  margin-right: 10px;
  position: relative;
}

.rightBarProfileImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.rightBarOnline {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: limegreen;
  position: absolute;
  top: -2px;
  right: 0;
  border: 2px solid white;
}

.rightBarUsername {
  font-weight: 500;
  font-size: 18px;
}

.rightBarFollowingName {
  margin-top: 8px;
  text-align: center;
}

.rightBarTitle {
  text-align: center;
}
